import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CommonModule } from '@angular/common';
import {NanniesComponent } from '../pages/nannies/nannies.component';
import { LandingComponent } from '../pages/landing/landing.component';
import { ScheduleNannyComponent } from '../pages/schedule-nanny/schedule-nanny.component';
import { NannyProfileComponent } from '../pages/nanny-profile/nanny-profile.component';
import { NanniesListComponent } from '../pages/nannies-list/nannies-list.component';
import { InterviewRequestComponent } from '../pages/interview-request/interview-request.component';
import { SuccessComponent } from '../pages/interview-request/success/success.component';


const routes: Routes = [
  {
    path: 'nannies',
    component:  NanniesComponent,
    // component:  NanniesListComponent,

  },
  {
    path: 'profile',
    component:  NannyProfileComponent,
  },

  {
    path: 'schedule',
    component:  ScheduleNannyComponent,
  },
  {
    path: 'interviewRequest',
    component:  InterviewRequestComponent,
  },
  {
    path: 'interviewSuccess',
    component:  SuccessComponent,
  },
  {
    path: 'home',
    component:  LandingComponent,
  },

{
  path: '',
  redirectTo: '/nannies',
  pathMatch: 'full'
},

];

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports:[RouterModule],
  declarations: []
})
export class AppRoutingModule { }



